.file {
  display: inline-block;
  background-repeat: no-repeat;
  background-position: left center;
  padding-left: 20px;
  min-height: 16px; }

[dir='rtl'] .file {
  background-position: right center;
  padding-right: 20px;
  padding-left: inherit; }

.file--general {
  background-image: url("../../images/icons/application-octet-stream.png"); }

